// client/src/pages/AdminDashboard.js
import React, { useState } from 'react';
import {
  Container,
  Typography,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Button,
  TextField,
} from '@mui/material';

const initialProducts = [
  { id: 1, name: 'Burger', price: 89.99 },
  { id: 2, name: 'Wings', price: 79.99 },
];

const AdminDashboard = () => {
  const [products, setProducts] = useState(initialProducts);
  const [form, setForm] = useState({ id: '', name: '', price: '' });

  const handleEdit = (product) => setForm(product);

  const handleDelete = (id) => setProducts(products.filter((p) => p.id !== id));

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = () => {
    if (form.id) {
      setProducts(products.map((p) => (p.id === form.id ? { ...form, id: p.id } : p)));
    } else {
      const newProduct = {
        ...form,
        id: products.length + 1,
        price: parseFloat(form.price),
      };
      setProducts([...products, newProduct]);
    }
    setForm({ id: '', name: '', price: '' });
  };

  return (
    <Container>
      <Typography variant="h4" sx={{ mt: 3 }}>Admin Dashboard</Typography>

      <TextField label="Name" name="name" value={form.name} onChange={handleChange} sx={{ mr: 2 }} />
      <TextField label="Price" name="price" type="number" value={form.price} onChange={handleChange} sx={{ mr: 2 }} />
      <Button variant="contained" onClick={handleSubmit}>{form.id ? 'Update' : 'Add'} Product</Button>

      <Table sx={{ mt: 4 }}>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Price</TableCell>
            <TableCell>Actions</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {products.map((product) => (
            <TableRow key={product.id}>
              <TableCell>{product.name}</TableCell>
              <TableCell>R{product.price.toFixed(2)}</TableCell>
              <TableCell>
                <Button size="small" onClick={() => handleEdit(product)}>Edit</Button>
                <Button size="small" color="error" onClick={() => handleDelete(product.id)}>Delete</Button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Container>
  );
};

export default AdminDashboard;
